body {
    margin: 0;
}

.top {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 10000000;
    font-size: 25px;
    color: white;
    background-color: rgb(27, 180, 160);
    height: 100px
}

.currentScore {
    color: white;
}

.logoRyan {
    height: 100px;
    width: 133px;
}
.score {
    position: relative;
    top: -100px;
    background-image: linear-gradient(to top, rgb(180, 29, 48), white);
    width: 100%;
    height: 450px;
}

.logo {
    position: relative;
    top: 175px;
    font-size: 50px;
    color: white;
}

.instructions {
    position: relative;
    top: 200px;
    font-size: 25px;
    color: white;
}

.tiles {
    margin: 5px 0px 5px 0px;
    border-radius: 10px;
    cursor: pointer;
}

.tiles:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 0px 12px 8px rgba(0,0,0,.25);
}
.innerFooter {
    position: absolute;
    bottom:0;
    height: 100px;
    width: 100%;
    background-color: rgb(27, 180, 160);
    margin-bottom: 0px;
}

.row1 {
    width: 100%;
}

.first {
    color: white;
    font-size: 28px;
}

.third {
    color: white;
    font-size: 28px;
}

html, body {
    min-height: 100%;
}

#root {
    min-height: 100%;
    position: relative;
    padding-bottom: 100px;
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
}

